import React from 'react';
import styles from './layout.module.css';
import { Header } from '../Header/Header';
import { Home } from '../Home/Home';
import { Contact } from '../Contact/Contact';
import { Plants } from '../Plants/Plants';
import { WishList } from '../WishList/WishList';
import { Search } from '../Search/Search';
import { ShoppingCart } from '../ShoppingCart/ShoppingCart';
import { Checkout } from '../ShoppingCart/Checkout';
import { Confirmation } from '../Confirmation/Confirmation';
import { Route, useLocation } from 'react-router-dom';

export const Layout = ({ menu, onMenuItemClick }) => {
  const location = useLocation();
  console.log('location', location);
  return (
    <>
      <Header />
      <Route path={`${location.pathname}/plants/:name`}>
        <div className={styles.plantContainer}>
          <Plants />
        </div>
      </Route>
      <Route path={`${location.pathname}/plants`} exact>
        <div className={styles.plantContainer}>
          <Plants />
        </div>
      </Route>
      <Route path={`${location.pathname}/contact`}>
        <div className={styles.plantContainer}>
          <Contact />
        </div>
      </Route>
      <Route path={`${location.pathname}/wishlist`}>
        <div className={styles.plantContainer}>
          <WishList />
        </div>
      </Route>
      <Route path={`${location.pathname}/search/:term`}>
        <div className={styles.plantContainer}>
          <Search />
        </div>
      </Route>
      <Route path={`${location.pathname}/cart`}>
        <div className={styles.plantContainer}>
          <ShoppingCart />
        </div>
      </Route>
      <Route path={`${location.pathname}/checkout`}>
        <div className={styles.plantContainer}>
          <Checkout />
        </div>
      </Route>
      <Route path={`${location.pathname}/confirm`}>
        <div className={styles.plantContainer}>
          <Confirmation />
        </div>
      </Route>
      <Route path={location.pathname} exact>
        <Home />
      </Route>
      <Route path={`${location.pathname}/home`} exact>
        <Home />
      </Route>
      <Route path={`${location.pathname}/index`} exact>
        <Home />
      </Route>
    </>
  );
};
